import "!style-loader!css-loader!github-markdown-css/github-markdown-light.css";
const readme = require("../readme.md").default as string;
const styles = require("!raw-loader!sass-loader!import-glob-loader!../scss/styles.scss").default;
const theme = require("!raw-loader!sass-loader!import-glob-loader!../scss/theme.scss").default;

const firstExample = require("!raw-loader!./example-theme.html").default;
const secondExample = require("!raw-loader!./example-styles.html").default;

const generateWrapper = (exampleWrapperTag, content) => {
    customElements.define(
        exampleWrapperTag,
        class extends HTMLElement {
            connectedCallback() {
                // eslint-disable-next-line @typescript-eslint/no-explicit-any
                const shadyCss = (window as any).ShadyCSS;
                if (shadyCss) {
                    shadyCss.styleElement(this);
                }
                if (!this.shadowRoot) {
                    this.attachShadow({ mode: "open" });
                    const template = document.createElement("template");
                    template.innerHTML = content;
                    if (shadyCss) {
                        shadyCss.prepareTemplate(template, exampleWrapperTag);
                    }
                    this.shadowRoot.appendChild(document.importNode(template.content, true));
                    requestAnimationFrame(() => this.dispatchEvent(new CustomEvent("ready")));
                }
            }
        }
    );
    return document.createElement(exampleWrapperTag);
};

const first = generateWrapper("sd-theme-wrapper", `<style>${theme}</style>${firstExample}`);
document.querySelector("#theme-example").appendChild(first);

const second = generateWrapper("sd-style-wrapper", `<style>${styles}</style>${secondExample}`);
document.querySelector("#styles-example").appendChild(second);

document.querySelector("#markdown-container").innerHTML = readme;
